<script>
import { fb } from "../firebase";
import { createUserWithEmailAndPassword, signInWithEmailAndPassword } from 'firebase/auth'
import { mapGetters } from 'vuex'
//const fb = require("../firebase");
    export default {
        name: 'Home',
        data(){
            return{
                dialog: false, username: null, password: null,
                mail: null, passcode: null, snack: false, snackColor: '', snackText: '',
                menus:[
                    {title:'Home', route:''},
                    {title:'About Us', route:''},
                    {title:'Products', route:''},
                    {title:'Contact Us', route:''},
                ],
                workingHours: [
                    'Monday - Friday: 7:30 - 16:30',
                    'Saturday: 7:30 - 16:30',
                    'Sunday and holidays: Closed',
                ],
                menu: false,
            }
        },
        computed: {
            ...mapGetters(['userCart']),
            cartItems() {
                return this.$store.state.userCart.length;
            },
            userCart: state => state.userCart,
            productInventory() {
                return this.$store.getters.productInventory;
            },
        },
        created () {
        this.$store.dispatch("getInventory");
        },
        methods: {
            
            scrollTo(section) {
                this.$refs[section].scrollIntoView({ behavior: 'smooth' });
            },
            async login() {
                signInWithEmailAndPassword(fb, this.username, this.password)
                .then(() => {
                    const { username } = this;
                    alert(username + " logged in")
                    this.$router.push({ name: "dashboard", params:{ username: username} });
                })
                .catch(function(error) {
                    //Handle errors here
                    var errorCode = error.code;
                    var errorMessage = error.message;
                    if (errorCode == 'auth/user-not-found') {
                        alert("User not found.");
                    }else {
                        alert(errorMessage);
                    }
                    alert(error);
                });
            },
            async register(){
            //export declare function createUserWithEmailAndPassword(auth: Auth, email: string, password: string): Promise<UserCredential>;
            createUserWithEmailAndPassword(fb, this.mail, this.passcode)
            .then(() => {
                alert('Sign Up Successful. Please LogIn.');
                //this.$router.replace('dashboard');
            })
                .catch(function(error) {
                    //Handle errors here
                    var errorCode = error.code;
                    var errorMessage = error.message;
                    if (errorCode == 'auth/weak.password') {
                        alert('The password is too weak.');
                    }else {
                        alert(errorMessage);
                    }
                    alert(error);
                });
          },
          viewCart() {
                this.$router.push({ name: "cart" });
            },
          async productSelect(product) {
            // Find the product in the cart
            const cartProduct = this.$store.state.userCart.find(p => p.ProductName === product.ProductName);
            // Find the product in the productInventory array
            const inventoryProduct = this.productInventory.find(p => p.ProductName === product.ProductName);
            if (cartProduct) {
            if (inventoryProduct) {
                // Check if the number of units in the cart are equal to number of units in stock
                if (cartProduct.Qty === Number(inventoryProduct.Quantity)){
                
                // Display an error message if the number of units in the cart match those that are in stock
                this.snackColor = 'error'
                this.snackText = 'Maximum number of units in stock reached. '
                this.snack = true
                return;
                }else{
                
                // If the number of units in the cart are less than units in stock then proceed increment units by one
                cartProduct.Qty++;
                cartProduct.TotalCost = cartProduct.UnitPrice * cartProduct.Qty;
                cartProduct.VAT = cartProduct.TotalCost * inventoryProduct.Tax/100;

                // Commit the pushProductToCart mutation to update the cart state
                this.$store.commit('pushProductTouserCart', cartProduct);
                }
            }
            
            } else {
            if (inventoryProduct) {
                // Check if the product is out of stock
                if (inventoryProduct.Quantity === '0'){
                    // Display an error message if the product is out of stock
                    this.snackColor = 'error'
                    this.snackText = 'Product Is Out of Stock. '
                    this.snack = true
                    return;
                }else{
                    this.$store.commit('pushProductTouserCart', {
                    ProductName: product.ProductName,
                    UnitPrice: product.SellingPrice,
                    Qty: 1,
                    TotalCost: product.SellingPrice * 1,
                    VAT: product.SellingPrice * inventoryProduct.Tax/100
                    });
                }
                
            }
            }
        }
        }
    };
    
</script>
